import {React, useState} from 'react';
import logo from './logo.svg';
import './App.css';
import {BrowserRouter as Router, Route, Routes,useNavigate} from 'react-router-dom';
import {useAuth0,Auth0Provider} from '@auth0/auth0-react';
import Box from '@mui/material/Box';
import Backdrop from '@mui/material/Backdrop';
import SpeedDial from '@mui/material/SpeedDial';
import SpeedDialIcon from '@mui/material/SpeedDialIcon';
import SpeedDialAction from '@mui/material/SpeedDialAction';
import FileCopyIcon from '@mui/icons-material/FileCopyOutlined';
import SaveIcon from '@mui/icons-material/Save';
import PrintIcon from '@mui/icons-material/Print';
import ShareIcon from '@mui/icons-material/Share';
import SearchIcon from '@mui/icons-material/Search';
import InfoIcon from '@mui/icons-material/Info';
import FiberNewIcon from '@mui/icons-material/FiberNew';
import LogoutIcon from '@mui/icons-material/Logout'


function App() {
  return (
    <Auth0Provider domain="kingtechnician.us.auth0.com" clientId="xTbGmROcQrGne2nH7AAEDu1C3E8hKeBW" authorizationParams={{
      redirect_uri:window.location.origin
    }}>
    <Router className="App">
      <Routes className="App-header">
        <Route path = "/" element = {<AuthForm/>}/>
        <Route path="/dashboard" element={<Dashboard/>}/>
        <Route path="/about" element={<About/>}/>
      </Routes>
    </Router>
    </Auth0Provider>
  );
}

function AuthForm()
{
  const {user,isAuthenticated,isLoading} = useAuth0();
  console.log(isAuthenticated)
  let navigate = useNavigate();
  const {loginWithRedirect} = useAuth0();
  if(isAuthenticated)
  {
    navigate("/dashboard")
  }
  return (
    <div className = "App">
      <div className = "App-header">
        <p>This is where the authentication information will go.</p>
        <button onClick ={loginWithRedirect}>Log In</button>
      </div>
    </div>
  )
}

function About()
{
  return (
  <div className = "App">
  <header className = "homePage App-header">
    <h3>Privacy, Security, and Usability</h3>
    <p>As a student at Virginia State University, have you wondered where you might be able to communicate your concerns, feedback, or desires for VSU's continued growth?</p>
    <p>VSU has numerous resources to ensure that greater happens here, but there remains to be a centralized platform for viewing a well-manaed tally of feedback from students.</p>
  </header>
</div>
  )
}

function Dashboard()
{
  const {user,isAuthenticated,isLoading,logout} = useAuth0();
  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  const [selected, setSelected] = useState(0);
  return (
    <div className = "App">
    <header className = "homePage App-header">
        <SpeedDial
          ariaLabel="SpeedDial example"
          sx={{position: 'absolute', width:'fit-content',height:'fit-content', bottom: 0,right:0}}
          icon={<SpeedDialIcon />}
          onClose={handleClose}
          onOpen={handleOpen}
          open={open}
          FabProps = {{
            sx:{
              bgcolor:'#193A6F',
              transition:'all 0.6s'
            }
          }}
        >
          <SpeedDialAction
            key="About"
            icon={<InfoIcon />}
            tooltipTitle="About"
            tooltipOpen
            onclick={handleClose}
            />
          <SpeedDialAction
            key="Search"
            icon={<SearchIcon />}
            tooltipTitle="Search"
            tooltipOpen
            onclick={handleClose}
            />
            <SpeedDialAction
            key= "New Post"
            icon={<FiberNewIcon />}
            tooltipTitle="New Post"
            tooltipOpen
            onclick={handleClose}
            />
          <SpeedDialAction
          key="Logout"
          icon={<LogoutIcon />}
          tooltipTitle="Logout"
          tooltipOpen
          onClick={() => logout({ logoutParams: { returnTo: window.location.origin } })}
          />
        </SpeedDial>
    </header>
  </div>
 
  )
}

export default App;
